.button {
    width: 100px;
    color: white;
    background-color: #6df84e;
    border-radius: 3px;
    border: none;
    padding: 10px;
    font-family: sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
}


.disabled {
    background-color: rgba(183, 183, 191, 0.83);
    color: rgba(120, 120, 124, 0.83);
}

.secondary {
    background-color: rgba(65, 65, 243, 0.83);
    color: white;
}

.default {
    background-color: rgb(15, 134, 43);
}

.red {
    background-color: #cc0000;
}

.default:hover {
    background-color: rgb(5, 80, 23);
}

.red:hover {
    background-color: #5c1f1f;
}

.secondary:hover {
    background-color: rgba(30, 30, 112, 0.83);
}


.default:active {
    background-color: rgb(164, 237, 181);
}

.secondary:active {
    background-color: rgba(166, 166, 232, 0.83);
}

.red:active {
    background-color: rgb(230, 116, 143);
}

.button:disabled {
    color: rgba(64, 64, 67, 0.83);
    background-color: rgba(155, 155, 163, 0.83);
}
