.buttonsContainer {
    display: flex;
    gap: 4px; /* Расстояние между кнопками */
    width: 50%; /* Ширина контейнера кнопок */
    margin-bottom: 20px; /* Отступ снизу */
}

.buttonsContainer button {
    flex: 1; /* Равномерное распределение ширины кнопок */
    height: 40px; /* Фиксированная высота кнопок */
    font-size: 16px; /* Размер текста кнопок */
    font-weight: bold; /* Жирный текст */
}

.responseContainer {
    display: flex;
    align-items: center; /* Выравнивание по вертикали */
    gap: 10px; /* Расстояние между картинкой и текстом */
}

.imageContainer {
    flex-shrink: 0; /* Запрет на сжатие картинки */
}

.image {
    width: 100%; /* Ширина картинки */
    height: auto; /* Автоматическая высота */
}

.textContainer {
    flex-grow: 1; /* Растягиваем текстовый блок на всю доступную ширину */
}

.code {
    font-size: 24px; /* Увеличенный размер текста */
    font-weight: bold; /* Жирный текст */
    margin-bottom: 10px; /* Отступ снизу */
}

.text {
    font-size: 18px; /* Увеличенный размер текста */
    font-weight: bold; /* Жирный текст */
    margin-bottom: 10px; /* Отступ снизу */
}

.info {
    font-size: 16px; /* Увеличенный размер текста */
    font-weight: bold; /* Жирный текст */
}