
.buttonsContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.clock {
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
    max-width: 300px;
    margin: 0 auto;
}

.watch {
    font-size: 24px;
    margin-bottom: 10px;
}

.watch strong {
    font-weight: bold;
}

.more {
    font-size: 16px;
    color: #666;
}
