.radio {
    /* Ваши стили для радио-кнопок */
    margin-right: 20px;
    position: relative;
    appearance: none; /* Убираем стандартный вид радио-кнопки */
    width: 20px;
    height: 20px;
    border: 2px solid #ccc;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
    background-color: white; /* Добавляем белый фон */
}

.radio::before {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: transparent; /* Прозрачный фон по умолчанию
    background-color: red; /* Красный цвет для демонстрации */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.radio:checked::before {
    background-color: #007bff; /* Синий цвет при выборе */
}

.label {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.options {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
}