.item {
    border-bottom: 1px solid #ddd;
    padding: 10px;
}

.item:last-of-type {
    border-bottom: none;
}

.users {
    width: 50%;
    border-collapse: collapse;
    margin-top: 30px;
}

.buttonsContainer {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
}

.container {
    padding: 20px;
    border: 1px solid #ddd;
    background-color: #f9f9f9;
}

.thead {
    background-color: #f2f2f2;
}

.nameCol, .ageCol {
    padding: 10px;
    text-align: left;
}

.nameCol {
    width: 60%;
}

.ageCol {
    width: 30%;
}