.input:focus {
}

.input {
    padding: 8px 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.3s;
}

.inputWrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.superInput {
    background-color: #f9f9f9;
}

.errorInput {
    border-color: red;
}

.errorInput:focus {
    border-color: darkred;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 4px;
}
