.loader {
    border: 16px solid #f3f3f3; /* Светло-серый цвет границы */
    border-top: 16px solid #3498db; /* Синий цвет верхней границы */
    border-radius: 50%; /* Делаем границу круглой */
    width: 120px; /* Ширина лоадера */
    height: 120px; /* Высота лоадера */
    animation: spin 2s linear infinite; /* Применяем анимацию */
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}